.profile {
  margin-top: 20px;
  display: flex;
}
.profile img {
  width: 50px;
  height: 50px;
}

.profileItem {
  margin-left: 30px;
  display: flex;
  flex-direction: column;
}

.profileItem ul {
  margin: 0;
  padding: 0;
  list-style: none;
}



.profileName {
  text-transform: capitalize;
  font-weight: bold;
  font-size: 30px;
  color: #000;
}

.social {
  margin-top: 20px;
}

.socialLink {
  text-decoration: none;
  color: firebrick;
  transition: all 0.3s;
}

.socialLink:hover {
  color: #ff1a1a;
}