.posts {
    margin-top: 20px;
}

.postsTitle {
    font-weight: bold;
    font-size: 24px;
}

.postsAdd {
    display: flex;
    align-items: flex-end;
}

.postsTextarea {
    outline: none;
    border: 1px solid firebrick;
    resize: none;
    width: 300px;
    height: 100px;
}

.postsTextarea::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}
.postsTextarea::-webkit-scrollbar-button:start:decrement,
.postsTextarea::-webkit-scrollbar-button:end:increment  {
    height: 10px;
    background-color: transparent;
}
.postsTextarea::-webkit-scrollbar-track-piece  {
    background-color: #ffffff;
    -webkit-border-radius: 16px;
}
.postsTextarea::-webkit-scrollbar-thumb:vertical {
    height: 10px;
    background-color: firebrick;
    border: 1px solid firebrick;
    -webkit-border-radius: 16px;
}

.postsBtn {
    cursor: pointer;
    padding: 15px 30px;
    margin-left: 20px;
    background-color: #fff;
    border: 2px solid firebrick;
    color: firebrick;
}

.postsBtn:active {
    background-color: rgba(220, 220, 220, 0.5);
}